  vertical-align:middle;
}

.home-span{
  font-size: calc(14px + (20 - 14) * ((100vw - 300px) / (1600 - 300)));
  line-height: calc(1.3em + (1.5 - 1.2) * ((100vw - 300px)/(1600 - 300)));
  padding: 5%;
  vertical-align:middle;
}

.contact-h1{
  font-size: calc(20px + (60 - 20) * ((100vw - 300px) / (1600 - 300)));
  line-height: calc(1.3em + (1.5 - 1.2) * ((100vw - 300px)/(1600 - 300)));
  padding: 5%;
  vertical-align:middle;
}

