}

#modded .progress {
  min-height: 36px;
  overflow: hidden;
  position: relative;
}
#modded .progress span {
  position: relative;
  float: left;
  color: #fff;
  padding: 8px;
  z-index: 99999;
}
#modded .progress span i {
  width: inherit;
  font-size: inherit;
  position: relative;
  top: 2px;
  margin-left: 8px;
}
#modded .progress .determinate {
  width: 0;
  transition: width 1s ease-in-out;
  padding: 8px;
  position: relative;
  color: #fff;
  text-align: right;
  white-space: nowrap;
}
#modded ul.collapsible {
  padding: 0;
  border: none;
  border-radius: 0;
  box-shadow: none;
}
#modded ul.collapsible li {
  margin-bottom: 14px;
}
#modded ul.collapsible .collapsible-header {
  padding: 0;
  border-bottom: 0;
  border-radius: 25px;
}
#modded ul.collapsible .collapsible-header .progress {
  margin: 0;
  border-radius: 0;
  border-radius: 25px;
}
#modded ul.collapsible .collapsible-body {
  padding: 10px;
  /* box-shadow: rgba(0, 0, 0, 0.137255) 0px 2px 2px 0px, rgba(0, 0, 0, 0.117647) 0px 3px 1px -2px, rgba(0, 0, 0, 0.2) 0px 1px 5px 0px; */
}

#modded ul.collapsible li{
  padding-bottom: 20px;
}

#modded ul.collapsible li img{
  float: left;
  margin-Top:-6px;
  margin-right: 20px;
  height: 45px;
  width: 45px;
}


/* center the blockquote in the page */
.blockquote-wrapper {
   display: flex;
   /* height: 100vh; */
   padding: 5% 5%;
}

/* Blockquote main style */
.blockquote {
    position: relative;
    font-family: 'Barlow Condensed', sans-serif;
    max-width: 620px;
    max-height: 300px;
    margin: 10px auto;
    
}

h5,.date{
  margin-left:15px;
  font-family: 'Fugaz One';font-size: 1.7rem;
}


.tag{
  position: relative;
  float: right;
  height: 40px;
  width: 0px;
  border-left: 15px solid #263238;
  border-right: 15px solid #263238;
  border-bottom: 9px solid transparent;
  right: 20px;
}
.chip-font-size{
  font-size: calc(10px + (14 - 10) * ((100vw - 300px) / (1600 - 300)));
}

.experience-p{
  font-size: 1.2rem;
  line-height: 1.5;
}

li.inlining{ 
  display: inline-block;
  margin-left: 20px 0;
}
.experience{
  margin:20px;
  border: 2px solid #263238;
  background-color:white;
}
.experience:hover{
-webkit-box-shadow: -1px 13px 35px 1px rgba(0,0,0,0.75);
-moz-box-shadow: -1px 13px 35px 1px rgba(0,0,0,0.75);
box-shadow: -1px 13px 35px 1px rgba(0,0,0,0.75);
}

.hovering:hover{
  -webkit-box-shadow: -1px 13px 35px 1px rgba(0,0,0,0.75);
  -moz-box-shadow: -1px 13px 35px 1px rgba(0,0,0,0.75);
  box-shadow: -1px 13px 35px 1px rgba(0,0,0,0.75);
  }

/* Blockquote header */
.blockquote h1 {
    font-family: 'Abril Fatface', cursive;
    position: relative; /* for pseudos */
    color: #e74848;
    font-size:  calc(14px + (20 - 14) * ((100vw - 300px) / (1600 - 300)));;
    font-weight: normal;
    line-height: 1.2;
    margin: 0;
    border: 3px solid #fff;
    border: solid 3px;
    border-radius:10px;
    padding: 15px;
}

/* Blockquote right double quotes */
.blockquote h1:after {
    content:"";
    position: absolute;
    border: 3px solid #e74848;
    border-radius: 0 50px 0 0;
    width: 60px;
    height: 60px;
    bottom: -60px;
    left: 50px;
    border-bottom: none;
    border-left: none;
    z-index: 3; 
}

.blockquote h1:before {
    content:"";
    position: absolute;
    width: 80px;
    border: 6px solid #37474f;
    bottom: -3px;
    left: 50px;
    z-index: 2;
}

/* increase header size after 600px */
@media all and (min-width: 600px) {
    .blockquote h1 {
        line-height: 1.2;
   }

}

/* Blockquote subheader */
.blockquote h4 {
    position: relative;
    color: #ffffff;
    font-size:  calc(10px + (14 - 10) * ((100vw - 300px) / (1600 - 300)));;
    font-weight: 400;
    line-height: 1.2;
    margin: 0;
    padding-top: 15px;
    z-index: 1;
    margin-left:150px;
    padding-left:12px;
}

span.blockquote {
  padding-right:20px;
  color: #ffffff;
  font-size: 1.2vw;
  font-weight: 400;
  line-height: 1.2;
  font-family: 'Barlow Condensed', sans-serif;
}

 
.blockquote h4:first-letter {
  margin-left:-12px;
}